:root {
  --ogcio-color--gold: #83764e;
  --ogcio-color--green: #004d44;
  --ogcio-color--white: #fff;
  --ogcio-spacing--md: 10px;
  --ogcio-spacing--lg: 16px;
  --ogcio-font-size--md: 16px;
  --ogcio-button--default__background-color: #fff;
  --ogcio-button--default__text-color: #004d44;
  --ogcio-button--default__font-size: 16px;
  --ogcio-button--default__padding-top: 10px;
  --ogcio-button--default__padding-bottom: 10px;
  --ogcio-button--default__padding-left: 16px;
  --ogcio-button--default__padding-right: 16px;
  --ogcio-button--secondary__background-color: #fff;
  --ogcio-button--secondary__text-color: #004d44;
  --ogcio-button--secondary__font-size: 16px;
  --ogcio-button--secondary__padding-top: 10px;
  --ogcio-button--secondary__padding-bottom: 10px;
  --ogcio-button--secondary__padding-left: 16px;
  --ogcio-button--secondary__padding-right: 16px;
  --ogcio-button--primary__background-color: #83764e;
  --ogcio-button--primary__text-color: #fff;
  --ogcio-button--primary__padding-top: 10px;
  --ogcio-button--primary__padding-bottom: 10px;
}

.ogcio-button {
  background-color: var(--ogcio-button--default__background-color);
  cursor: pointer;
  color: var(--ogcio-button--default__text-color);
  font-size: var(--ogcio-button--default__font-size);
  line-height: var(--ogcio-button--default__font-size);
  padding-top: var(--ogcio-button--default__padding-top);
  padding-bottom: var(--ogcio-button--default__padding-bottom);
  border: 0;
}

.ogcio-button--primary {
  background-color: var(--ogcio-button--primary__background-color);
  color: var(--ogcio-button--primary__text-color);
}

